// Panels
// 
// BASELESS Panels components, use them as you would any panel.
// 
// Markup: docs/panels.hbs
// 
// .primary - primary panel color
// .success - success panel color
// .warning - warning panel color
// .utility - utility panel color
// .rounded.default - round panel
//  
// Styleguide components.panels.

.panel-accent(@bg: @color-grey-default, @border: @color-grey-default-accent, @color: @color-grey-darkest) {
  border: 1px solid @border;
  .panel-heading {
    color: @color;
    background: @bg;
    border-bottom: 1px solid @border;
  }
  .panel-list-item {
    border-top: 1px solid @border;
    &.hover {
      &:hover {
        background: mix(@color-light, @bg, 50%); 
      }
    }
  }
}

.@{css-prefix}panel {

  .reset-spacing(0,0);
  .clearfix();

  .panel-heading,
  .panel-body,
  .panel-list-item,
  .panel-notice {
    .reset-spacing(0, @elem-default-padding);
  }

  .panel-heading {
    font-weight: @typo-font-weight-heavy;
    font-size: @typo-font-size-small;
  }

  .panel-body {
    background: @color-light;
  }

  .panel-foot {
    .reset-spacing(0, 5px @elem-default-padding);
    font-size: 75%;
  }

  .panel-list {
    .panel-list-item {
      &:first-child, &.first {
        border-top: none;
      }
    }
  }

  .panel-accent();
  
  &.utility {
    .panel-accent(@color-card-warm-grey, @color-card-warm-grey-border, @color-grey-darkest);
  }

  &.primary {
    .panel-accent(@color-primary, darken(@color-primary, 10%), @color-light);
  }

  &.success {
    .panel-accent(@color-success, darken(@color-success, 10%), @color-light);
  }

  &.warning {
    .panel-accent(@color-warning, darken(@color-warning, 10%), @color-light);
  }
  
  // &.notice {
  //   .panel-accent(@color-notice, darken(@color-notice, 10%), @color-light);
  // }

  // &.info {
  //   .panel-accent(@color-info, darken(@color-info, 10%), @color-light);
  // }

  // &.inverted {
  //   .panel-accent(@color-inverted, darken(@color-inverted, 10%), @color-light);
  // }

}